.responsive {
  max-width: 100%;
  height: 80px;
}

.scrolling-wrapper-flexbox {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  &::-webkit-scrollbar {
    display: none;
  }
}
.card {
  flex: 0 0 auto;
  margin: 20px;
  width: 300px;
}

